  display: table;
}
.row::after {
  clear: both;
}
.col-6 {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
  float: left;
  width: 100%;
}
@media (min-width: 768px) {
  .col-6 {
    width: 50%;
  }
}
.cf {
  zoom: 1;
