.cf::after {
  clear: both;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
}
body {
  color: #111;
  font-family: Helvetica, sans-serif;
}
code {
  color: #eee;
  background-color: #111;
  padding: 1px 5px;
  border-radius: 3px;
  white-space: nowrap;
}
pre code {
  display: block;
  overflow: auto;
  padding: 15px 5px;
  white-space: pre;
}
.lead {
  font-size: 22px;
  font-weight: 200;
}
.styled {
  font-weight: 400;
}
a {
  transition: color 0.1s ease-in-out;
}
html,
body {
  background: #eee;
  min-height: 100%;
}
html {
  position: relative;
}
body {
  height: 100%;
}
body {
  padding-bottom: 75px;
}
a {
  color: #67a8dc;
  text-decoration: none;
}
a:hover {
  color: #90c0e6;
}
hr {
  border: 0;
  border-top: 1px solid #111;
}
ul,
ol {
  zoom: 1;
  margin-bottom: 0;
}
ul::before,
ol::before,
ul::after,
ol::after {
  content: '';
  display: table;
}
ul::after,
ol::after {
  clear: both;
}
.page-header {
  background-color: #111;
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom: 8px solid #67a8dc;
}
.logo {
  display: inline-block;
  width: 25px;
  height: 25px;
}
.logo img {
  width: 100%;
  height: auto;
}
.main-nav {
  font-size: 14px;
  text-align: center;
  float: right;
  height: 25px;
}
.nav-item {
  text-decoration: none;
  padding: 0 15px;
  line-height: 25px;
  height: 100%;
  display: inline-block;
  color: #eee;
}
.nav-item:hover {
  color: #d6d6d6;
}
@media (min-width: 768px) {
  .logo {
    width: 50px;
    height: 50px;
  }
  .main-nav {
    height: 50px;
    font-size: 18px;
  }
  .nav-item {
    line-height: 50px;
    padding: 0 30px;
  }
}
.page-footer {
  background: #111;
  color: #eee;
  font-size: 14px;
  position: absolute;
  bottom: 0;
  width: 100%;
}
.copyright {
  float: left;
}
.footer-links {
  list-style: none;
  padding: none;
  margin: none;
  float: right;
}
@media (min-width: 768px) {
  .page-footer {
    font-size: 18px;
  }
}
.list {
  padding-left: 0;
  list-style: none;
}
.list-item {
  margin-bottom: 15px;
  border-bottom: 5px solid #67a8dc;
}
.list-item h4 {
  margin: 0;
}
.list-item h3 {
  margin: 5px 0;
}
.list-item a {
  display: block;
  color: #111;
  text-decoration: none;
}
.date {
  margin: 10px 0;
  background-color: #676868;
  color: #eee;
  display: inline-block;
  padding: 6px 10px;
  border-radius: 10px;
}
.home h1,
.home .lead {
  text-align: center;
}
